
body {
  color: #7c7c7c;
  /*文字色*/
  font-family: "M PLUS 1p";
  font-size: 120%;
}
.title {
  font-family: "Fredoka One", cursive;
  font-size: 200%;
}
a.title:visited {
  color: #f4cfe2;
}
a:visited {
  color: #7c7c7c;
}
header a {
  margin: 5px;
}
.add-button {
  text-align: center;
}
.main-content-box {
  width: 80%;
  margin: 0 auto;
  max-width: 1000px;
}
.talknig-conponent,
.config-select-component,
.input-form-component {
  height: 40%;
}
.v-enter-active,
.v-leave-active {
  -webkit-transition: opacity 1500ms;
  transition: opacity 1500ms;
}
.v-enter,
.v-leave-to {
  opacity: 0;
}
.show-config-component {
}
.template-save {
  text-align: right;
}

.start-button {
  text-align: center;
}
.start {
  background-color: #f8bad7;
}
.end {
  background-color: #f8bad7;
}
.end {
  -webkit-animation: blink 1s ease-in-out infinite alternate;
  animation: blink 1s ease-in-out infinite alternate;
}
@-webkit-keyframes blink {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes blink {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}

.discription {
}

.img {
  text-align: center;
}

.input-contents {
  text-align: left;
}
.input-contents-label {
  text-align: right;
}
.input-width {
  width: 100%;
}

.col-sm-3 {
  text-align: center;
}
.scroll {
  height: 400px;
  overflow-y: scroll;
  margin-bottom: 1px;
}
.hover:hover {
  background-color: #f5dbe7;
}

.input-text {
  padding: 0.5em 1em;
  height: 10em;
  font-weight: bold;
  color: #7c7c7c; /*文字色*/
  background: #fff;
  border: solid 3px #f8bad7; /*線*/
  border-radius: 10px; /*角の丸み*/
}
.input-text p {
  margin: 0;
  padding: 0;
  font-family: "B612 Mono", monospace;
}

.template-label {
  text-align: right;
}
.template-selector {
  text-align: center;
  width: 100%;
}
.template-button {
  text-align: center;
}

.col-sm-4 {
  text-align: center;
}
.scroll {
  height: 400px;
  overflow-y: scroll;
  margin-bottom: 1px;
}
.hover:hover {
  background-color: #f5dbe7;
}














